const parseDate = (input_date) => {
    const weirdDate = new Date(input_date);

    const dateOptions = {
        day: "numeric",
        month: "2-digit",
        year: "2-digit"
    };
    const timeOptions = {
        hour: "2-digit",
        minute: "2-digit"
    }
    const normalDate = weirdDate.toLocaleString("default", dateOptions);
    const time = weirdDate.toLocaleString("default", timeOptions)
    return ` ${time} ${normalDate}`
}
export default parseDate