import React, { Component } from "react";
import Form from "react-bootstrap/Form";
import * as api from "../utils/api";
import { Redirect } from "@reach/router";

class PostArticle extends Component {
	state = {
		postedArticleId: 0,
	};

	submitArticle = (event) => {
		event.preventDefault();
		const title = event.target[0].value;
		const body = event.target[1].value;
		const topic = event.target[2].value;
		const author = "jessjelly";
		const article = { author, title, body, topic: topic.toLowerCase() };
		api.postArticle(article);
	};
	render() {
		return this.state.postedArticleId ? (
			<Redirect to={`/articles/${this.state.postedArticleId}`} />
		) : (
			<form onSubmit={this.submitArticle}>
				<Form.Group className="PostArticle">
					<Form.Control placeholder="Title" />
					<Form.Control id="body" placeholder="" />
					<div id="select">
						<Form.Label id="label">Post to:</Form.Label>
						<Form.Control id="dropdown" as="select">
							<option>Coding</option>
							<option>Cooking</option>
							<option>Football</option>
						</Form.Control>
					</div>

					<button type="submit">Submit</button>
				</Form.Group>
			</form>
		);
	}
}

export default PostArticle;
