import React from "react";
import { Link } from "@reach/router";
import Voter from "./Voter";
import parseDate from "../utils/parseDate";
const ArticleCard = (props) => {
	const { article_id, author, title, topic, created_at, votes, comment_count } =
		props;
	const date = parseDate(created_at);

	return (
		<div className="ArticleCard">
			<Link to={`/article/${article_id}`}>
				{" "}
				<h4>{title} </h4>
			</Link>
			<div className="ArticleCardBody">
				<p>
					<Link to={`/users/${author}`}>
						<b>{author}</b>
					</Link>{" "}
					in {topic} {date}
				</p>
				<Link to={`/article/${article_id}`}>{comment_count} comments</Link>
			</div>
			<div id="home">
				<Voter element_id={article_id} votes={votes} element={"article"} />
			</div>
		</div>
	);
};

export default ArticleCard;
